import React from 'react';
import { Container, Segment, Menu, Tab } from 'semantic-ui-react';
import TheBasicsComponent from './TheBasicsComponent';


const panes = [
    {
      menuItem: 'The Basics',
      render: () => <Tab.Pane attached={false}><TheBasicsComponent/></Tab.Pane>,
    },
    {
      menuItem: 'React',
      render: () => <Tab.Pane attached={false}>React</Tab.Pane>,
    }
  ]
  
class SkillsPage extends React.Component {
    render () {
        return (
            <Container>
                <Menu widths={4} className="skill-menu" stackable fluid>
                    <Menu.Item
                        name='skills'
                        onClick={this.handleItemClick}
                    >
                            Frontend
                    </Menu.Item>
                    <Menu.Item
                        name='skills'
                        onClick={this.handleItemClick}
                    >
                        Backend
                    </Menu.Item>
                    <Menu.Item
                        name='reviews'
                        onClick={this.handleItemClick}
                    >
                            Dev Ops
                    </Menu.Item>
                    <Menu.Item
                        name='reviews'
                        onClick={this.handleItemClick}
                    >
                            Soft Skills
                    </Menu.Item>
                </Menu>
                <Segment>
                    <Tab menu={{ secondary: true, pointing: true, stackable: true, widths: 2 }} panes={panes} />
                </Segment>
            </Container>
        );
    }
}

export default SkillsPage;